.outer_div {
  background-image: url(../../../images/mountains.jpg);
  width: 100%;
  display: flex;
  margin-bottom: -9px;
  color: #ffffff;
}
.btn {
  color: #ffffff;
  padding-left: 0;
}
.btn:hover {
  color: #ffffff;
}
.btn p {
  margin: 0;
}
.text_left_div_in {
  text-align: right;
}

.left_div {
  margin: 2% 0;
  padding: 4%;
  width: 70%;
  border-right: 3px solid #ffffff;
}

.right_div {
  width: 100%;
  margin: 2% 0;
}

.button_of_div_2 {
  margin-top: -1%;
  margin-bottom: 2%;
}

.right_div_in {
  padding: 4% 0;
  margin-left: 10%;
  height: 100%;
}

.rd_text {
  font-size: 35px;
  margin: 0;
}

.rd_bottom {
  font-size: 35px;
  margin: 0;
}

.rd_bottom_text {
  margin-top: 0%;
  margin-bottom: 0;
}
.rd_bottom_most_text {
  margin-top: 2%;
}

@media (max-width: 775px) {
  .outer_div {
    display: inline-block;
    width: 100%;
  }
  .text_left_div_in {
    text-align: center;
  }
  .left_div {
    border-right: 0;
    margin-right: 0 2%;
    margin: 1%;
    border-bottom: 2px solid #ffffff;
    width: 98%;
  }
  .rd_text {
    text-align: center;
  }
  .button_of_div_2 {
    text-align: center;
  }
  .rd_bottom {
    text-align: center;
  }
  .rd_bottom_text {
    text-align: center;
  }
  .rd_bottom_most_text {
    text-align: center;
  }
  .right_div_in {
    margin-left: 0;
  }
}
